import Vue from 'vue'
import Vuex from 'vuex'
import storage from'@/storage'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    //token
    jwt:'',
    //123
    identity:0,
  },
  mutations: {
    set_jwt(state,jwt){
      state.jwt=jwt;
      storage.set('jwt',jwt);
    },
    set_identity(state,identity){
      state.identity=identity;
      storage.set('identity',identity);
    },
    del_all(state){
      state.identity=0;
      state.jwt='';
      storage.removeAll();
    }
  },
  getters: {
    get_jwt(){
      return storage.get('jwt');
    },
    get_identity(){
      return storage.get('identity');
    }
  },
  actions: {
  },
  modules: {
  }
})
